import { GameState } from "../core/game_state";

export class WegameSplashState extends GameState {
    constructor() {
        super("WegameSplashState");
    }

    getInnerHTML() {
        return `
        <div class="wrapper">
            <strong></strong>
            <div>,</div>
            <div>,</div>
            <div>,</div>
            <div>,</div>
        </div>
`;
    }
    onEnter() {
        setTimeout(
            () => {
                this.app.stateMgr.moveToState("PreloadState");
            },
            G_IS_DEV ? 1 : 6000
        );
    }
}
